.features-section {
  padding: 40px;
  background-color: #f9f9f9;
  text-align: center;
}


.features-slider {
  margin-top: 20px;
}

.feature-card {
  padding: 20px;
  margin: 10px;
  height: 250px;
  width: 300px !important;
  border-radius: 10px;
  /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); */
  transition: transform 0.3s ease-in-out;
  text-align: center;
}

.feature-card:hover {
  transform: translateY(-40px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.feature-icon {
  font-size: 4rem;
  color: #007bff;
  margin-bottom: 10px;
  animation: flip 2s infinite ease-in-out;
}



.feature-description {
  font-size: 1rem;
  color: black;
  font-weight: 600;
  line-height: 1.5;
}

.features-title{
  font-size: 50px;
   color: orangered;
   font-weight: 800;
   
}

.feature-title{
  font-size: 25px;
   color: orangered;
   font-weight: 900;
   
}
.features-title span{
  font-size: 40px;
  color: black !important;
  font-weight: 800;
  
}
/* Base Style for Icons */
.animateIcon {
  display: inline-block;
  font-size: 60px;
  color: black;
  animation: flip 2s infinite ease-in-out; /* Apply flipping animation */
}
.feature-icon{
  color: orange;
  font-size: 70px;
}

/* Keyframes for Flip Animation */
@keyframes flip {
  0% {
    transform: rotateY(0deg); /* Initial position */
  }
  50% {
    transform: rotateY(180deg); /* Halfway flipped */
  }
  100% {
    transform: rotateY(360deg); /* Full flip */
  }
}

h1 {
  font-size: 2.5rem;
  font-weight: bold;
  position: relative;
  display: inline-block;
  overflow: hidden;
  animation: fadeInText 1.5s ease forwards;
  color: #333; /* Adjust based on your design */
}

h1::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px; /* Adjust thickness */
  background: linear-gradient(
    to right,
    rgba(255, 105, 180, 0.8), 
    rgba(75, 192, 192, 0.8), /* Teal */
    rgba(255, 206, 86, 0.8), /* Yellow */
    rgba(54, 162, 235, 0.8) /* Blue */
  );
  background-size: 200% 100%;
  animation: underlineAnimation 1.5s ease-in-out infinite alternate;
}

@keyframes underlineAnimation {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
