
.cards {
    background: #ffffff;
    border: 1px solid #ddd;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease-in-out;
    margin-top: 40px;
    width: 280px;
}

.cards:hover {
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.card-img-top {
    width: 90%;
    margin: auto;
    height: 200px;
    object-fit: cover;
    border-bottom: 1px solid #ddd;
}

/* Card Body */
.card-bodys {
    padding: 15px;
    text-align: center;
     
}

/* Card Title */
.card-titles {
    font-size: 1.2rem;
    font-weight: 800;
    margin-bottom: 10px;
    color: black !important;
}

/* Button Styling */
.details-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 8px 12px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
    margin-top: 20px;
}

.details-button:hover {
    background-color: #0056b3;
}


.btnnager{
    border: none;
    outline: none;
    background-color: orangered;
    color: white;
    font-weight: 500;
    font-size: 18px;
    padding: 5px 10px;
    width: 100%;
    border-radius: 5px;
}