.container {
  margin-top: 20px;
}

.tabs {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

button.active {
  background-color: #007bff;
  color: white;
}

button {
  padding: 10px 15px;
  border-radius: 5px;
  background-color: #f4f4f4;
  border: 1px solid #ccc;
}

.card {
  border: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card-img-top {
  height: 200px;
  object-fit: cover;
}

.card-body {
  text-align: center;
  padding: 10px;
}

.card-text {
  font-size: 14px;
  color: #555;
}

.tabs {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.tabs button {
  padding: 10px 20px;
  margin: 0 5px;
  cursor: pointer;
  border: 1px solid #ddd;
  background-color: #f4f4f4;
  transition: background-color 0.3s ease;
}

.tabs button.active {
  background-color: #007bff;
  color: white;
}

.service-benefits {
  margin-bottom: 40px;
}

.service-benefits h3 {
  font-size: 1.5rem;
  margin-bottom: 20px;
}



.service-benefits li {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: orange;
  font-weight: 300;
  font-family: "Crimson Text", serif;
}

.service-benefits ul{
       list-style-type: none;
}
