.footer-container {
  background-color: orange !important;
  color: white;
  padding: 50px 0;
}

.footer-container a {
  color: black;
  text-decoration: none;
}

.footer-container a:hover {
  color: #f0ad4e;
}

.social-icons .social-icon {
  font-size: 2rem;
  color: wheat;
  transition: color 0.3s;
}

.social-icons .social-icon:hover {
  color: #f0ad4e;
}

.social-icons a {
  margin-right: 20px;
}

.list-unstyled li {
  font-size: 1.1rem;
  margin-bottom: 10px;
}

.footer-bottom {
  font-size: 0.9rem;
  color: #f0f0f0;
}

.footer-container h5 {
  font-size: 1.4rem;
  margin-bottom: 15px;
}
