/* General Container Styling */
.locationcont {
  background: linear-gradient(135deg, #f9f9f9, #e6f7ff);
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

/* Top Section Styling */
.top-section {
  margin-bottom: 2rem;
}

.experience {
  font-size: 2.8rem;
  font-weight: bold;
  color: orange;
  animation: superAnimation 1.5s ease-in-out forwards;
}
.experience span{
  font-size: 2.5rem;
  font-weight: bold;
  color: gray;
  animation: bounce 1.5s ease-in-out 0.5s forwards;
}
@keyframes superAnimation {
  0% {
    opacity: 0;
    transform: scale(0.5) rotate(0deg); 
  }
  50% {
    opacity: 1;
    transform: scale(1.2) rotate(15deg);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(0deg); 
  }
}

@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(10px); /* Start from slightly below */
  }
  100% {
    opacity: 1;
    transform: translateY(0); /* Move to the original position */
  }
}

@keyframes bounce {
  0% {
    opacity: 0;
    transform: translateY(10px) scale(0.8); /* Start below and smaller */
  }
  50% {
    opacity: 1;
    transform: translateY(-10px) scale(1.2); /* Bounce up and grow */
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1); /* Final position and normal size */
  }
}

.tagline {
  font-size: 2rem;
  font-weight: 600;
  color: black;
}

.one-stop {
  font-size: 1.2rem;
  color: #555;
  margin-top: 0.5rem;
}

.one-stop span {
  font-weight: bold;
  color: #ff6f61;
}

/* Heading Styling */
h1 {
  font-size: 3rem;
  font-weight: bold;
  color: #ff6f61;
  margin-bottom: 1.5rem;
}

h1 span {
  color: black;
}

/* Location Image */
.location-img img {
  width: 100%;
  height: auto;
  border-radius: 15px;
  filter: drop-shadow(0 5px 15px rgba(0, 0, 0, 0.2));
  transition: transform 0.3s ease-in-out;
}

.location-img img:hover {
  transform: scale(1.05);
}



.location-card:hover {
  transform: translateY(-10px);
}

.location-card {
  background: orange;
  border: none;
  border-radius: 15px;
  padding: 1.5rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  height: 130px !important;
  border-top: 1px solid black; /* Start with transparent border */
  transition: all 0.3s ease-in-out;
}
.tagline span{
  color: #ff6f61;
  font-size: 2.2rem;
  font-weight: 700;

}




.card-title {
  font-size: 1.8rem;
  font-weight: bold;
  color: white;
}

.card-text {
  font-size: 1.3rem;
  color: black;
  font-weight: 600;
  padding: 2px;
}

/* Icon Animation */
.animateicons {
  animation: bounce 1.5s infinite ease-in-out;
  color: orangered;
  font-size: 2rem;
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .experience,
  .tagline {
    font-size: 1.5rem;
  }

  .card-title {
    font-size: 1.5rem;
  }

  .card-text {
    font-size: 1rem;
  }
}
