.card-div {
  position: relative;
  overflow: hidden;
  height: 150px;

}

.carousel-container .card-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
  color: #333;
  text-align: center;
 
}
.carousel-container .card-img{
  align-items: center !important;
  justify-content: center !important;
  animation: dynamicAnimation;
}
.carousel-container .card-image{
  align-items: center !important;
  justify-content: center !important;
}



/* Keyframes for dynamic animations */
@keyframes dynamicAnimation {
  0% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
  50% {
    transform: scale(1) rotate(10deg);
    opacity: 1;
  }
 
  75% {
    transform: scale(1.1) rotate(20deg);
    opacity: 1;
  }
  100% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
}

/* Add hover effect for an interactive boost */
.card:hover .card-image {
  animation: hoverEffect 1s ease infinite;

 
}
.card-image{
  width: 200px !important;
}

@keyframes hoverEffect {
  0% {
    transform: scale(1.1) rotate(0deg);
  }
  50% {
    transform: scale(1.15) rotate(5deg);
  }
  100% {
    transform: scale(1.1) rotate(0deg);
  }
}

.carousel-container .card{
  border: none;
  width: 300px;
  border-radius: 20%;
 box-shadow: 1px 1px 0 1px black;

}


.why-choose {
  padding: 40px 20px;
}

.whychoose .card {
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 20px 0;
