  /* Slider container styling */
  .slider-container {
    width: 90%; /* Reduce container width to 80% of the viewport */
    position: relative;
    margin: auto;
    padding: 20px 0;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Optional: shadow effect */
    background: #ffffff; /* Slider background color */
    border-radius: 10px; /* Rounded corners for a clean look */
  }

  /* Ensuring slide alignment */
  .slider-container .slick-slide {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  /* Arrow button styling */
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    background-color: #007bff; /* Light background for the buttons */
    border: 1px solid #007bff;
    color: white;
    border-radius: 50%; /* Circular buttons */
    cursor: pointer;
    width: 60px;
    opacity: 2;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
  }

  .slick-arrow:hover {
    background-color: #007bff; 
    color: #ffffff; 
    opacity: 1;
  }

  .slick-arrow.next {
    right: 10px; /* Position next arrow */
  }

  .slick-arrow.prev {
    left: 10px; /* Position prev arrow */
  }

  /* Dots below slider */
  .slick-dots {
    bottom: 10px; /* Position dots closer to slider */
  }

  .slick-dots li button:before {
    color: #007bff; /* Dot color */
    font-size: 12px;
  }

  .slick-dots li.slick-active button:before {
    color: #0056b3; /* Active dot color */
  }

  /* Adjust slider on smaller screens */
  @media (max-width: 768px) {
    .slider-container {
      width: 95%; /* Full width on smaller screens */
    }

    .slide-image {
      height: 350px; /* Adjust height for smaller screens */
    }

    .slick-arrow {
      width: 30px;
      height: 30px;
    }
  }

  /* ImageSlider.css */
.slider-container {
    max-width: 100%;
    margin: auto;
  }
  
  .slide-image  {
    width: 100%;
    height: 600px;
    object-fit: cover; /* Makes sure the content covers the space without distortion */
  }
  .slide-video{
    height: 600px;
  }
  
  